<template>
  <!--页面根元素-->
  <div class="idm_render_engine_container">
    <!--页面渲染的容器-->
    <div id="idm_render_engine_page_container">
      <!--弹出框容器-->
      <div id="idm_dialog_wrap" v-show="dialogVisibleStatus" class="idm_dialog_wrap_preview" :hideing="`${dialogIsHideing?'hideing':''}`">
        <div class="idm_dialog_shade_layer" :class="'idm_dialog_shade_layer_'+item.packageid" v-for="(item,index) in dialogObjectList" :key="index" :dialog-id="item.packageid" @click="layerClickHandle"></div>
      </div>
      <div
        class="idm_page_root"
        :id="'PAGE_' + PageSettingData.id"
        :idm-com-anchor-id="PageSettingData.id"
        ref="idm_page_root"
        :class="`${PageSettingData.id}`"
      ></div>
    </div>
    <a-drawer
      :title="pagePropData.drawerTitleShow?pagePropData.drawerTitle:''"
      :placement="pagePropData.drawerPlacement||'right'"
      :closable="pagePropData.drawerClosable===false?false:true"
      :destroyOnClose="pagePropData.drawerDestroyOnClose"
      :maskClosable="pagePropData.drawerMaskClosable===false?false:true"
      :mask="pagePropData.drawerMask===false?false:true"
      :keyboard="pagePropData.drawerKeyboarde===false?false:true"
      :maskStyle="commonCustomEventFunHandle(pagePropData,'drawerMaskStyleFunction')||{}"
      :wrapStyle="commonCustomEventFunHandle(pagePropData,'drawerWrapStyleFunction')||{}"
      :drawerStyle="commonCustomEventFunHandle(pagePropData,'drawerStyleFunction')||{}"
      :headerStyle="commonCustomEventFunHandle(pagePropData,'drawerHeaderStyleFunction')||{}"
      :bodyStyle="commonCustomEventFunHandle(pagePropData,'drawerBodyStyleFunction')||{}"
      :width="pagePropData.drawerWidth||256"
      :height="pagePropData.drawerHeight||256"
      :zIndex="pagePropData.drawerZIndex||1000"
      :wrapClassName="(pagePropData.drawerWrapClassName||'')+' idm_module_setting_drawer'"
      :visible="drawerVisible"
      @close="drawerOnClose"
      @afterVisibleChange="drawerOnAfterVisibleChange"
    >
      <iframe class="idm_drawer_iframe_basestyle" :src="drawerIframeUrl" name="idm_drawer_iframe"/>
    </a-drawer>
  </div>
</template>

<script>
//页面渲染引擎所需要的组件注册
import "@/plugins/preview";
export default {
  name: "Preview",
  components: {},
  data() {
    return {
      pageid: "",
      //当前页面的数据
      PageSettingData: {},
      dialogVisibleStatus:false,
      //是否正在隐藏中
      dialogIsHideing:false,
      //窗口的对象
      dialogObjectList:[],
      //控制中心窗口是否显示
      drawerVisible:false,
      //控制中心窗口iframe的URL
      drawerIframeUrl:"",
      //控制中心窗口的option
      drawerControlSetPanelOption:null,
      //页面属性
      pagePropData:{}
    };
  },
  mounted() {
    this.windowResizeEvent(function(){
      //给所有组件发送pageResize消息
      IDM.broadcast.send({
        type:"pageResize",
        rangeModule:"",
        message:IDM.getClientWH()
      })
    })
  },
  created() {
    this.pageid = this.$route.params.pageid;
    //直接判断pageid如果为devpreview则是预览开发环境的
    if (this.pageid == "devpreview") {
      //取缓存数据
      this.PageSettingData = JSON.parse(window.localStorage["devpreview"]);
      console.log("当前预览的缓存数据", this.PageSettingData);
      this.pageid = this.PageSettingData.id;
      this.loadPageElement();
    } else {
      this.$previewApi
        .fetchPageSettingDataForVersion({
          pageid: this.pageid,
          version: "", //当前的版本，没有则必须要返回最新的，有版本要返回对应的版本结果
        })
        .then((res) => {
          //获取页面配置数据返回数据结果
          this.PageSettingData = res.data.data;
          console.log("获取页面配置数据返回数据结果", this.PageSettingData);
          this.loadPageElement();
        });
    }
    this.injectInnerActionToWindow();
  },
  methods: {
    /**
     * 窗口大小更改事件
     */
    windowResizeEvent(callback) {
        window.onresize = function() {
            var target = this;
            if (target.resizeFlag) {
                clearTimeout(target.resizeFlag);
            }
            target.resizeFlag = setTimeout(function() {
                callback();
                target.resizeFlag = null;
            }, 100);
        }
    },
    /**
     * 控制中心抽屉关闭事件
     */
    drawerOnClose() {
      this.drawerVisible = false;
      this.commonCustomEventFunHandle(null,"drawerCloseFunction");
    },
    /**
     * 控制中心切换抽屉时动画结束后的回调	
     */
    drawerOnAfterVisibleChange() {
        this.commonCustomEventFunHandle(null,"drawerAfterVisibleChangeFunction");
    },
    /**
     * 组件加载之前执行
     */
    beforeLoadingFunction(){
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (propData.beforeLoadingFunction&&propData.beforeLoadingFunction.length>0) {
        this.commonCustomEventFunHandle(propData,"beforeLoadingFunction");
      }
    },
    /**
     * 组件加载完后执行
     */
    afterLoadingFunction(){
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (propData.afterLoadingFunction&&propData.afterLoadingFunction.length>0) {
        this.commonCustomEventFunHandle(propData,"afterLoadingFunction");
      }
    },
    /**
     * 遮罩层直接关闭窗口
     */
    layerClickHandle(e){
      let dialogId = e.target.getAttribute("dialog-id");
      if(!dialogId){
        return;
      }
      this.IdmBuiltin_CloseDialog({
        targetModule:[{moduleId:dialogId}]
      })
    },
    injectInnerActionToWindow(){
      window.IdmBuiltin_OpenControlSetPanel = this.IdmBuiltin_OpenControlSetPanel;
      window.IdmBuiltin_CloseControlSetPanel = this.IdmBuiltin_CloseControlSetPanel;
      window.IdmBuiltin_OpenUrl = this.IdmBuiltin_OpenUrl;
      window.IdmBuiltin_OpenDialog = this.IdmBuiltin_OpenDialog;
      window.IdmBuiltin_CloseDialog = this.IdmBuiltin_CloseDialog;
      window.IdmBuiltin_ShowModule = this.IdmBuiltin_ShowModule;
      window.IdmBuiltin_HideModule = this.IdmBuiltin_HideModule;
    },
    /**
     * 打开组件控制面板设置
     * {
     *   controlSetPanelOption:{
     *     param:{
     *        marketModuleId:"必填，市场组件的ID，组件内取this.moduleObject.comId即可",
     *        pageId:"必填，页面的ID，直接使用 window.IDM.broadcast.pageModule.id 即可获取",
     *        packageid:"必填，组件实例的ID，主要用于区别页面的同一个组件，直接使用 this.moduleObject.packageid 即可获取到"
     *     },
     *     showTop:默认为true，设置是否显示在最顶级页面中，这样做的目的是为了在跨页面的时候遮罩显示在最外面，能覆盖整个窗口
     *     success:层弹出后的成功回调方法,
     *     yes:确定按钮回调方法,
     *     cancel:关闭触发的回调,
     *     reset:重置后的回调,
     *     other:其他按钮触发回调方法,
     *     controlSetPanelId:"控制面板窗口ID,每次打开的时候自动生成"
     *   }
     * }
     */
    IdmBuiltin_OpenControlSetPanel(ctx){
      let iframeUrl = this.pagePropData.moduleControlCenterSettingUrl;
      //生成url
      if(!iframeUrl){
        return;
      }
      iframeUrl=iframeUrl+(iframeUrl.indexOf("?")==-1?"?":"&");
      if(ctx&&ctx.controlSetPanelOption){
        if(ctx.controlSetPanelOption.param
        &&ctx.controlSetPanelOption.param.marketModuleId
        &&ctx.controlSetPanelOption.param.pageId
        &&ctx.controlSetPanelOption.param.packageid){
          Object.keys(ctx.controlSetPanelOption.param).forEach((key,index)=>{
            if(index>0){
              iframeUrl=iframeUrl+"&";
            }
            iframeUrl=iframeUrl+key+"="+ctx.controlSetPanelOption.param[key];
          })
        }else{
          return;
        }
      }
      this.drawerIframeUrl = IDM.url.getWebPath(iframeUrl);
      //设置对应的option
      this.drawerControlSetPanelOption = ctx.controlSetPanelOption||null;
      this.drawerVisible = true;
      //执行打开成功的回调方法
      this.drawerControlSetPanelOption&&this.drawerControlSetPanelOption.success&&this.drawerControlSetPanelOption.success(ctx.controlSetPanelOption.controlSetPanelId);
    },
    /**
     * 关闭组件控制面板设置，这里要判断controlSetPanelId是否与当前页面的一致，一致才能关闭
     * {
     *   controlSetPanelOption:{
     *     controlSetPanelId:"控制面板窗口ID"
     *   }
     * }
     */
    IdmBuiltin_CloseControlSetPanel(ctx){
      if((ctx&&ctx.innerClose)||(ctx&&ctx.controlSetPanelOption&&ctx.controlSetPanelOption.controlSetPanelId&&this.drawerControlSetPanelOption&&ctx.controlSetPanelOption.controlSetPanelId==this.drawerControlSetPanelOption.controlSetPanelId)){
        //是关闭这个窗口，执行关闭事件
        this.drawerOnClose();
        //判断关闭的类型
        switch (ctx.actionType) {
          case "close":
          case "other":
            this.drawerControlSetPanelOption&&this.drawerControlSetPanelOption.other&&this.drawerControlSetPanelOption.other();
            break;
          case "commit":
            this.drawerControlSetPanelOption&&this.drawerControlSetPanelOption.yes&&this.drawerControlSetPanelOption.yes();
            break;
          case "reset":
            this.drawerControlSetPanelOption&&this.drawerControlSetPanelOption.reset&&this.drawerControlSetPanelOption.reset();
            break;
        }
      }
    },
    /**
     * 打开地址
     */
    IdmBuiltin_OpenUrl(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      window.open(IDM.url.getWebPath(customParam.openUrl),customParam.openName,customParam.openFeatures)
    },
    /**
     * 打开窗口
     * 要给对应的组件发送打开窗口的消息
     */
    IdmBuiltin_OpenDialog(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      var moduleIdArray = [];
      let existsCount = 0;
      customParam.targetModule.forEach(item=>{
        moduleIdArray.push(item.moduleId);
        //去加载
        const dialogObject = this.renderDialog(item.moduleId);
        if(dialogObject===false){
          return;
        }
        dialogObject.dialogIsHideing = false;
        if(ctx.layerOption){
          dialogObject.layerOption=ctx.layerOption;
        }
        //追加到窗口集合中
        this.dialogObjectList.push(dialogObject);
        existsCount++;
        $("#"+item.moduleId).css("display","flex");
      });
      //没有找到其中一个就不往下加载了
      if(existsCount==0){
        return;
      }
      this.dialogVisibleStatus = true;
      this.dialogIsHideing = false;
      let that = this;
      this.$nextTick(function(){
        //这里即使发送消息也没关系（会存储在等候消息池中），因为会等待组件加载完成再次调用发送消息
        setTimeout(() => {
          that.sendBroadcastMessage({
              type:"linkageOpenDialog",
              message:ctx,
              rangeModule:moduleIdArray
            })
        }, 5);
      })
      if(ctx.callback){
        ctx.callback();
      }
    },
    /**
     * 关闭窗口
     * 要给对应的组件发送关闭窗口的消息
     */
    IdmBuiltin_CloseDialog(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      var moduleIdArray = [];
      customParam.targetModule.forEach(item=>{
        moduleIdArray.push(item.moduleId);
        this.dialogObjectList.forEach((ditem,dindex)=>{
          if(ditem.packageid==item.moduleId){
            ditem.dialogIsHideing = true;
            $(".idm_dialog_shade_layer_"+item.moduleId).attr("hideing","hideing");
            if(ditem.layerOption){
              ditem.layerOption.cancel&&ditem.layerOption.cancel(ditem);
            }
            //如果是确认按钮调用的关闭则去调用yes方法
            if(ctx.isOkEvent){
              ditem.layerOption&&ditem.layerOption.yes&&ditem.layerOption.yes(ditem);
            }
          }
        })
      });
      if(this.dialogObjectList.length<=moduleIdArray.length){
        this.dialogIsHideing = true;
      }
      let that = this;
      that.sendBroadcastMessage({
          type:"linkageCloseDialog",
          message:ctx,
          rangeModule:moduleIdArray
        })
      setTimeout(() => {
        customParam.targetModule.forEach(item=>{
          $("#"+item.moduleId).css("display","none");
          that.destroyDialog(item.moduleId);
          //从dialogObjectList中删除
          if(that.dialogObjectList.length==1){
            that.dialogObjectList = [];
            return;
          }
          let existsIndex = -1;
          that.dialogObjectList.forEach((ditem,dindex)=>{
            if(ditem.packageid==item.moduleId){
              existsIndex = dindex;
              if(ditem.layerOption){
                ditem.layerOption.end&&ditem.layerOption.end(ditem);
              }
            }
          })
          if (existsIndex > -1) {
            //存在就清除
            that.dialogObjectList.splice(existsIndex, 1);
          }
        });
        if(that.dialogObjectList.length==0){
          that.dialogVisibleStatus = false;
          that.dialogIsHideing = false;
        }
      }, 500);
      if(ctx.callback){
        ctx.callback();
      }
    },
    /**
     * 显示组件
     * 直接用packageid隐藏
     */
    IdmBuiltin_ShowModule(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      var moduleIdArray = [];
      customParam.targetModule.forEach(item=>{moduleIdArray.push(item.moduleId);$("#"+item.moduleId).show()});
      this.sendBroadcastMessage({
          type:"linkageShowModule",
          message:ctx,
          rangeModule:moduleIdArray
        })
      if(ctx.callback){
        ctx.callback();
      }
    },
    /**
     * 隐藏组件
     * 直接用packageid显示
     */
    IdmBuiltin_HideModule(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      var moduleIdArray = [];
      customParam.targetModule.forEach(item=>{moduleIdArray.push(item.moduleId);$("#"+item.moduleId).hide();});
      this.sendBroadcastMessage({
          type:"linkageHideModule",
          message:ctx,
          rangeModule:moduleIdArray
        })
      if(ctx.callback){
        ctx.callback();
      }
    },
    /**
     * 组件通信：发送消息的方法
     * @param {
     *  type:"自己定义的，统一规定的type：linkageResult（组件联动传结果值）、linkageDemand（组件联动传需求值）、linkageReload（联动组件重新加载）
     * 、linkageOpenDialog（打开弹窗）、linkageCloseDialog（关闭弹窗）、linkageShowModule（显示组件）、linkageHideModule（隐藏组件）、linkageResetDefaultValue（重置默认值）"
     *  message:{实际的消息对象},
     *  rangeModule:"为空发送给全部，根据配置的属性中设定的值（值的内容是组件的packageid数组），不取子表的，比如直接 this.$root.propData.compositeAttr["attrKey"]（注意attrKey是属性中定义的bindKey）,这里的格式为：['1','2']"",
     *  className:"指定的组件类型，比如只给待办组件发送，然后再去过滤上面的值"
     *  globalSend:如果为true则全站发送消息，注意全站rangeModule是无效的，只有className才有效，默认为false
     * } object 
     */
    sendBroadcastMessage(object){
        window.IDM.broadcast&&window.IDM.broadcast.send(object);
    },
    /**
     * 同步方式加载应用信息和用户信息
     * 并且需要判断一下是否已经设置了登录判断，如果设置了则需要确定用户ID是否为空，为空则跳转到设置好的登录地址
     */
    async initAppAndUserInfo(propDataObject) {
      let that = this;
      let propData = propDataObject.compositeAttr;
      if (propData.openFetchApplicationInfo && propData.applicationInfoUrl) {
        await this.$previewApi
          .fetchApplicationInfo(propData.applicationInfoUrl, {
            pageid: this.pageid,
            ...this.commonParam()
          })
          .then((res) => {
            IDM.app.setAppInfo(res.data);
          })
          .catch(function (error) {});
      }
      if (propData.openFetchUserInfo && propData.userInfoUrl) {
        await this.$previewApi
          .fetchUserInfo(propData.userInfoUrl, {
            pageid: this.pageid,
            ...this.commonParam()
          })
          .then((res) => {
            IDM.user.setCurrentUserInfo(res.data);
          })
          .catch(function (error) {});
      }
      let switchUserInfoFiledValue = "";
      if(propData.switchUserInfo&&propData.switchUserInfoFiledName){
        let filedExp = propData.switchUserInfoFiledName,echoDataName="userObject";
        var dataObject = {IDM:window.IDM};
        dataObject[echoDataName] = IDM.user.getCurrentUserInfo();

        if(filedExp){
          filedExp = echoDataName+(filedExp.startsWiths("[")?"":".")+filedExp;
          switchUserInfoFiledValue = window.IDM.express.replace.call(this, "@["+filedExp+"]", dataObject);
        }
      }else if(propData.switchUserInfo){
        switchUserInfoFiledValue = IDM.user.getCurrentUserInfo()&&IDM.user.getCurrentUserInfo().data?IDM.user.getCurrentUserInfo().data.userid:"";
      }
      //判断是否需要登录(这里暂时写死data.userid)
      if (propData.switchUserInfo && !switchUserInfoFiledValue) {
        if (propData.loginPageUrl) {
          //跳转
          window.location.href = IDM.url.getWebPath(propData.loginPageUrl);
        } else {
          window.location.href = "";
        }
      }

      //应用主题：接口的方式
      if(propData.openIdmTheme&&propData.themeFetchType=="customInterface"&&propData.themeInterfaceUrl){
        await this.$previewApi
          .fetchThemeInfo(propData.themeInterfaceUrl, {
            pageid: this.pageid,
            ...this.commonParam()
          })
          .then((res) => {
            that.themeInterfaceHandle(res.data,"resultData")
          })
          .catch(function (error) {});
      }else if(propData.openIdmTheme&&propData.themeFetchType=="customFunction"&&propData.themeCustomFunction&&propData.themeCustomFunction.length>0){
        //自定义处理函数
        let switchThemeValue = "";
        propData.themeCustomFunction.forEach(sitem=>{
          try {
            switchThemeValue = window[sitem.name]&&window[sitem.name].call(this,{
              ...this.commonParam(),
              customParam:sitem.param,
              _this:that
            });
          } catch (error) {
          }
        })
        IDM.theme.setCurrentThemeInfo(switchThemeValue);
      }
    },
    /**
     * 自定义接口与页面接口统一回调处理主题的函数
     */
    themeInterfaceHandle(resultData,echoDataName){
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      let themeDataFiled = propData.themeDataFiled,
      themeDataFunction = propData.themeDataFunction,
      switchThemeValue=resultData;
      if(themeDataFiled){
        let filedExp = themeDataFiled;
        var dataObject = {IDM:window.IDM};
        dataObject[echoDataName] = resultData;

        if(filedExp){
          filedExp = echoDataName+(filedExp.startsWiths("[")?"":".")+filedExp;
          switchThemeValue = window.IDM.express.replace.call(this, "@["+filedExp+"]", dataObject);
        }
      }
      if(themeDataFunction&&themeDataFunction.length>0){
        var paramObject = {
              ...that.commonParam(),
              _this:that
            };
          paramObject[echoDataName] = resultData;
        themeDataFunction.forEach(sitem=>{
          try {
            switchThemeValue = window[sitem.name]&&window[sitem.name].call(this,{customParam:sitem.param,...paramObject});
          } catch (error) {
          }
        })
      }
      IDM.theme.setCurrentThemeInfo(switchThemeValue);
    },
    /**
     * 加载页面组件
     */
    async loadPageElement() {
      //追加js脚本
      if(this.PageSettingData.page.actions&&this.PageSettingData.page.actions.compiled){
       var pageScriptDom = $("<script type=\"text/javascript\" id=\"idm_page_script\"/>");
       $("body").append(pageScriptDom.html("var exports={};"+this.PageSettingData.page.actions.compiled))
      }
      //把map中的数据更新下
      this.PageSettingData.page.componentsMap.forEach((item) => {
        item.env = "production";
      });
      //把页面数据注册到消息桥接器中
      this.IDM.broadcast.registerModule(this.PageSettingData);
      //修改页面标题
      document.title = this.PageSettingData.pageBaseInfo.title || "IDM预览";
      //先加载应用信息
      await this.initAppAndUserInfo(this.PageSettingData.page.layout.props);
      //组件开始加载之前执行
      this.beforeLoadingFunction();
      //页面的属性渲染
      this.pageAttrPropHandle(this.PageSettingData.page.layout.props);
      //执行组件加载
      this.$nextTick(function () {
        this.initModuleTreeToPage(null, true, this.PageSettingData.page.layout);
      });
    },
    /**
     * 请求页面设置的所有接口并回调给每个组件
     */
    requestPageCommonInterfaceData(){
      //调用桥接器页面加载(包括实时渲染)完成事件
      this.IDM.broadcast.pageLoadCompleteAfter();

      if(this.isloadPageCommonInterface){
        return;
      }
      this.isloadPageCommonInterface = true;
      console.log("页面首屏组件已全部加载完毕！！！")
      this.afterLoadingFunction();
      var interfaceList = (this.PageSettingData.page.layout.props.compositeAttr?this.PageSettingData.page.layout.props.compositeAttr.interfaceList:[])||[];
      if(interfaceList.length==0){
        return;
      }
      //先获取页面ID，然后再获取所有的组件ID
      var pageId = this.pageid;
      var moduleIdArray = [],componentIdArray=[];
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if(item.packageid!=pageId){
          moduleIdArray.push(item.packageid);
          componentIdArray.push(item.comId);
        }
      });
      //所有地址的url参数转换
      var params = {
        idmPageId:pageId,
        moduleIds:moduleIdArray.join(","),
        componentIds:componentIdArray.join(",")
      };
      let urlObject = IDM.url.queryObject();
      Object.keys(urlObject).forEach((key)=>{
        params[key]=urlObject[key];
      })
      this.orderlyRequestInterface(interfaceList,0,params);
    },
    /**
     * 有序的加载接口并且回调各个组件
     */
    orderlyRequestInterface(interfaceList,index,params){
      if(interfaceList.length<=index){
        return;
      }
      var item = interfaceList[index];
      index = index+1;
      let that = this;
      // preAllowFunction
      if(item.preAllowFunction&&item.preAllowFunction.length>0){
        try {
          var itemParam = {...that.commonParam(),...item.preAllowFunction[0].param};
          let _isPass = window[item.preAllowFunction[0].name]&&window[item.preAllowFunction[0].name].call(this,itemParam);
          if(_isPass===false){
            that.orderlyRequestInterface(interfaceList,index,params);
            return;
          }
        } catch (error) {
        }
      }
      if(item.interfaceMode=="post"&&item.interfaceUrl){
          if(item.formDataFunction&&item.formDataFunction.length>0){
            try {
              var itemParam = {...that.commonParam(),...item.formDataFunction[0].param};
              let formDataParam = window[item.formDataFunction[0].name]&&window[item.formDataFunction[0].name].call(this,itemParam);
              params = {...params,...formDataParam}
            } catch (error) {
            }
          }
          this.$previewApi
          .postPageInterfaceInfo(item.interfaceUrl,params,{
            headers:item.interfaceContentType?{
              "Content-Type": item.interfaceContentType||"application/json;charset=UTF-8"
            }:{}
          })
          .then((res) => {
            //res.data
            //循环调用组件接收数据的方法
            that.callbackModuleSetContext(item,res.data);
            that.orderlyRequestInterface(interfaceList,index,params);
          })
          .catch(function (error) {
            that.orderlyRequestInterface(interfaceList,index,params);
          });
        }else if(item.interfaceMode=="get"&&item.interfaceUrl){
          this.$previewApi
          .getPageInterfaceInfo(item.interfaceUrl,params)
          .then((res) => {
            //res.data
            //循环调用组件接收数据的方法
            that.callbackModuleSetContext(item,res.data);
            that.orderlyRequestInterface(interfaceList,index,params);
          })
          .catch(function (error) {
            that.orderlyRequestInterface(interfaceList,index,params);
          });
        }else{
          that.orderlyRequestInterface(interfaceList,index,params);
        }
    },
    /**
     * 回调组件的设置值的方法
     */
    callbackModuleSetContext(item,resData){
      this.PageSettingData.page.componentsMap.forEach((sitem) => {
                  sitem.idmSetContextValue&&sitem.idmSetContextValue({
                              type:"pageCommonInterface",
                              key:item.resultDataName,
                              data:resData
                          })
            });
      //自定义处理返回结果函数
      if(item.interfaceRunLaterFunction&&item.interfaceRunLaterFunction.length>0){
        var interfaceRunLaterFunction = item.interfaceRunLaterFunction;
        interfaceRunLaterFunction.forEach(sitem=>{
          try {
            window[sitem.name]&&window[sitem.name].call(this,{
              ...this.commonParam(),
              customParam:sitem.param,
              resultData:resData,
              _this:this
            });
          } catch (error) {
          }
        })
      }
      let propData = this.PageSettingData.page.layout.props.compositeAttr||{};
      //应用主题：页面结果集
      if(propData.openIdmTheme&&propData.themeFetchType=="pageInterfaceResult"&&propData.themeDataName&&item.resultDataName==propData.themeDataName){
        this.themeInterfaceHandle(resData,propData.themeDataName)
      }
    },
    /**
     * 渲染页面的样式
     */
    pageAttrPropHandle(propDataObject) {
      let propData = propDataObject.compositeAttr;
      this.pagePropData = propData;
      var styleObject = {};
      if (propData.bgSize && propData.bgSize == "custom") {
        styleObject["background-size"] =
          (propData.bgSizeWidth
            ? propData.bgSizeWidth.inputVal + propData.bgSizeWidth.selectVal
            : "auto") +
          " " +
          (propData.bgSizeHeight
            ? propData.bgSizeHeight.inputVal + propData.bgSizeHeight.selectVal
            : "auto");
      } else if (propData.bgSize) {
        styleObject["background-size"] = propData.bgSize;
      }
      if (propData.positionX && propData.positionX.inputVal) {
        styleObject["background-position-x"] =
          propData.positionX.inputVal + propData.positionX.selectVal;
      }
      if (propData.positionY && propData.positionY.inputVal) {
        styleObject["background-position-y"] =
          propData.positionY.inputVal + propData.positionY.selectVal;
      }
      for (const key in propData) {
        if (propData.hasOwnProperty.call(propData, key)) {
          const element = propData[key];
          if (!element && element !== false) {
            continue;
          }
          switch (key) {
            case "width":
            case "height":
              styleObject[key] = element;
              break;
            case "bgColor":
              if (element.hex8) {
                styleObject["background-color"] = element.hex8;
              }
              break;
            case "box":
              if (element.marginTopVal) {
                styleObject["margin-top"] = `${element.marginTopVal}`;
              }
              if (element.marginRightVal) {
                styleObject["margin-right"] = `${element.marginRightVal}`;
              }
              if (element.marginBottomVal) {
                styleObject["margin-bottom"] = `${element.marginBottomVal}`;
              }
              if (element.marginLeftVal) {
                styleObject["margin-left"] = `${element.marginLeftVal}`;
              }
              if (element.paddingTopVal) {
                styleObject["padding-top"] = `${element.paddingTopVal}`;
              }
              if (element.paddingRightVal) {
                styleObject["padding-right"] = `${element.paddingRightVal}`;
              }
              if (element.paddingBottomVal) {
                styleObject["padding-bottom"] = `${element.paddingBottomVal}`;
              }
              if (element.paddingLeftVal) {
                styleObject["padding-left"] = `${element.paddingLeftVal}`;
              }
              break;
            case "bgImgUrl":
              styleObject["background-image"] = `url(${IDM.url.getWebPath(
                element
              )})`;
              break;
            case "icoImgUrl":
              //IDM.url.getWebPath(element)
              var link = document.createElement('link');
              link.type = 'image/x-icon';
              link.rel = 'shortcut icon';
              link.href = IDM.url.getWebPath(element);
              document.getElementsByTagName('head')[0].appendChild(link);
              break;
            case "positionX":
              //背景横向偏移

              break;
            case "positionY":
              //背景纵向偏移

              break;
            case "bgRepeat":
              //平铺模式
              styleObject["background-repeat"] = element;
              break;
            case "bgAttachment":
              //背景模式
              styleObject["background-attachment"] = element;
              break;
            case "border":
              if (element.border.top.width > 0) {
                styleObject["border-top-width"] =
                  element.border.top.width + element.border.top.widthUnit;
                styleObject["border-top-style"] = element.border.top.style;
                if (element.border.top.colors.hex8) {
                  styleObject["border-top-color"] =
                    element.border.top.colors.hex8;
                }
              }
              if (element.border.right.width > 0) {
                styleObject["border-right-width"] =
                  element.border.right.width + element.border.right.widthUnit;
                styleObject["border-right-style"] = element.border.right.style;
                if (element.border.right.colors.hex8) {
                  styleObject["border-right-color"] =
                    element.border.right.colors.hex8;
                }
              }
              if (element.border.bottom.width > 0) {
                styleObject["border-bottom-width"] =
                  element.border.bottom.width + element.border.bottom.widthUnit;
                styleObject["border-bottom-style"] =
                  element.border.bottom.style;
                if (element.border.bottom.colors.hex8) {
                  styleObject["border-bottom-color"] =
                    element.border.bottom.colors.hex8;
                }
              }
              if (element.border.left.width > 0) {
                styleObject["border-left-width"] =
                  element.border.left.width + element.border.left.widthUnit;
                styleObject["border-left-style"] = element.border.left.style;
                if (element.border.left.colors.hex8) {
                  styleObject["border-left-color"] =
                    element.border.left.colors.hex8;
                }
              }

              styleObject["border-top-left-radius"] =
                element.radius.leftTop.radius +
                element.radius.leftTop.radiusUnit;
              styleObject["border-top-right-radius"] =
                element.radius.rightTop.radius +
                element.radius.rightTop.radiusUnit;
              styleObject["border-bottom-left-radius"] =
                element.radius.leftBottom.radius +
                element.radius.leftBottom.radiusUnit;
              styleObject["border-bottom-right-radius"] =
                element.radius.rightBottom.radius +
                element.radius.rightBottom.radiusUnit;
              break;
            case "font":
              styleObject["font-family"] = element.fontFamily;
              if (element.fontColors.hex8) {
                styleObject["color"] = element.fontColors.hex8;
              }
              styleObject["font-weight"] =
                element.fontWeight && element.fontWeight.split(" ")[0];
              styleObject["font-style"] = element.fontStyle;
              styleObject["font-size"] =
                element.fontSize + element.fontSizeUnit;
              styleObject["line-height"] =
                element.fontLineHeight +
                (element.fontLineHeightUnit == "-"
                  ? ""
                  : element.fontLineHeightUnit);
              styleObject["text-align"] = element.fontTextAlign;
              styleObject["text-decoration"] = element.fontDecoration;
              break;
            case "layout":
              if (element.display && element.display == "flex") {
                styleObject["display"] = "flex";
                if (element.direction) {
                  styleObject["flex-direction"] = element.direction;
                }
                if (element.direction) {
                  styleObject["align-items"] = element.align;
                }
                if (element.direction) {
                  styleObject["justify-content"] = element.justify;
                }
              } else if (element.display) {
                styleObject["display"] = element.display;
              }
              break;
            case "pageMinWidth":
              if(element.inputVal&&element.selectVal){
                styleObject["min-width"] = element.inputVal+element.selectVal;
              }
              break;
          }
        }
      }
      this.$nextTick(function (params) {
        if (propData.watermarkType && propData.watermarkType != "none") {
          var _watermarkFont = this.$replace(propData.watermarkFont, {
              IDM: IDM,
            }),
            _watermarkFont1 = this.$replace(propData.watermarkFont1, {
              IDM: IDM,
            });
          //文字水印
          IDM.watermark.set(
            this.$refs.idm_page_root,
            propData.watermarkZindex,
            propData.watermarkType != "font"
              ? propData.watermarkImg
              : _watermarkFont,
            propData.watermarkType == "image" ? "" : _watermarkFont1,
            {
              fontColor:
                propData.watermarkColor && propData.watermarkColor.hex8,
              fontSize: `${propData.watermarkFontSize}px`,
              width: propData.watermarkSize,
              height: propData.watermarkSize,
              rotate: propData.watermarkRotate,
              imgSize: propData.watermarkImgSize,
              imgLeftSize: propData.watermarkImgLeftSize,
              fontLeftSize: propData.watermarkFontLeftSize,
              opacity: propData.watermarkOpacity / 100,
              topSize: propData.watermarkTopSize,
            }
          );
        } else {
          IDM.watermark.close(this.$refs.idm_page_root);
        }
      });
      IDM.setStyleToPageHead("PAGE_" + this.PageSettingData.id, styleObject);
      //加载外部js和css
      if(propData.resourceList&&propData.resourceList.length>0){
        propData.resourceList.forEach(item=>{
          if(item.resourceType=="js"){
            IDM.module.loadJs(IDM.url.getWebPath(item.resourceUrl),true,function(){

            },{async:item.async,defer:item.defer})
          }else{
            IDM.module.loadCss(IDM.url.getWebPath(item.resourceUrl),true,function(){

            })
          }
        })
      }
    },
    /**
     * 渲染加载指定ID的窗口
     * 首先取出对应的数据，判断是否
     */
    renderDialog(moduleId){
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        moduleId
      );
      if(!opModule){
        return false;
      }
      if(opModule.renderType=="firstrender"||opModule.renderType=="activerender"){
        opModule.renderLicence = true;
        if(opModule.renderType=="firstrender"&&opModule.loaded){
          return _.cloneDeep(opModule);
        }else{
          //实时渲染加载之前要把页面组件加载状态设置为false，否则实时渲染的组件消息通信无法准确接收到消息，需要等全部加载完再接收消息
          this.IDM.broadcast.pageModuleLoadCompleteBefore();
          //修改成全部未加载
          this.resetLayoutInnerModuleLoadedStatus(opModule);
          this.initModuleTreeToPage(opModule)
        }
      }
      return _.cloneDeep(opModule);
    },
    /**
     * 销毁指定ID的窗口
     */
    destroyDialog(moduleId){
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        moduleId
      );
      if(opModule&&opModule.renderType=="activerender"){
        $("#"+moduleId).remove();
      }
    },
    /**
     * 加载组件树到页面中，递归逐级加载
     * moduleData：要渲染的组件数据树结构
     * ischildren：是否为子组件数据
     * fModule：子组件所在的父级容器组件
     * addNextForModulePackageid：要追加到哪个组件的下面（非子级）的packageid
     */
    initModuleTreeToPage(
      moduleData,
      ischildren,
      fModule,
      addNextForModulePackageid
    ) {
      let that = this;
      moduleData = moduleData || that.PageSettingData.page.layout.children;
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      //循环取出当前一级未加载的组件
      var loadModule;
      moduleData.forEach((item) => {
        if(item.comType=="dialog"&&item.renderLicence!==true){
          let dialogRenderType = item.props.compositeAttr.dialogRenderType;
          //beforehand：不用判断直接放行可以进行渲染  firstrender：按需首次渲染  activerender：激活时渲染
          item.renderType = dialogRenderType;
          //renderLicence：渲染许可，true表示可以进行渲染，false表示暂时不渲染跳过
          item.renderLicence = !(dialogRenderType=="firstrender"||dialogRenderType=="activerender");
          if(!item.renderLicence){
            return;
          }
        }
        if (!item.loaded && !loadModule) {
          loadModule = item;
          item.loaded = true;
        }
      });
      if (!loadModule) {
        //一级没有待加载的，再去加载子级
        moduleData.forEach((item) => {
          if(item.comType=="dialog"&&!item.renderLicence){
            //渲染许可为false跳过不渲染下面的子级
            return;
          }
          if (item.children && item.children.length > 0) {
            that.initModuleTreeToPage(item.children, true, item);
          }
        });
      } else {
        //追加容器
        var comObject = loadModule;
        comObject.mountComplete = this.moduleMountComplete;
        comObject.moduleReload = this.moduleReload;
        //追加div
        var newComDiv = that.createModuleDom(comObject);
        var containerId = "";
        if(comObject.comType=="dialog"){
          containerId = that.PageSettingData.id;
          $("#idm_dialog_wrap").append(newComDiv);
        }else if (addNextForModulePackageid) {
          //添加到下方
          containerId = addNextForModulePackageid;
          $("#" + containerId).after(newComDiv);
        } else if (ischildren && fModule) {
          containerId = fModule.packageid;
          if (containerId == that.PageSettingData.id) {
            $("#PAGE_" + containerId).append(newComDiv);
          } else {
            var containerDom = $("#" + containerId);
            containerDom.removeClass("drag_container_empty");
            var containerInnerDom = containerDom.find(">*[idm-ctrl]");
            containerInnerDom.show();
            if (comObject.containerIndex || comObject.containerIndex == "0") {
              //添加内部容器，同时要删除drag_container_empty
              var innercontainer = containerInnerDom.find(
                ".drag_container[idm-ctrl-id='" +
                  fModule.id +
                  "'][idm-container-index=" +
                  comObject.containerIndex +
                  "]"
              );
              innercontainer&&innercontainer.append(newComDiv);
              innercontainer&&innercontainer.removeClass("drag_container_empty");
            } else {
              containerInnerDom.append(newComDiv);
            }
          }
        }
        //关联ID
        comObject.env = "production";
        that.IDM.module.addModuleToQueue(comObject);
      }

      return loadModule;
    },
    /**
     * 判断是否加载完成,未完成返回false，主要用于拷贝的时候递归加载完成的判断
     */
    pageModuleDataIsLoaded(moduleData, isnotloadinner) {
      let that = this;
      var isLoaded = true;
      moduleData = moduleData || that.PageSettingData.page.layout.children;
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      moduleData.forEach((item) => {
        if(item.comType=="dialog"&&!item.renderLicence){
          //渲染许可为false跳过不渲染下面的子级
          return;
        }
        if (!item.loaded) {
          isLoaded = false;
          // if(isnotloadinner&&(item.containerIndex||item.containerIndex=="0")){
          //   isLoaded = true;
          // }
        }
      });
      if (isLoaded) {
        moduleData.forEach((item) => {
          if(item.comType=="dialog"&&!item.renderLicence){
            //渲染许可为false跳过不渲染下面的子级
            return;
          }
          if (item.children && item.children.length > 0 && isLoaded) {
            isLoaded = this.pageModuleDataIsLoaded(
              item.children,
              isnotloadinner
            );
          }
        });
      }
      return isLoaded;
    },
    /**
     * 创建组件dom外部元素
     */
    createModuleDom(comObject) {
      let that = this;
      //包裹最外层的ID
      comObject.packageid = comObject.packageid || "component_" + IDM.UUID();
      //组件内部ID，这里可能不受核心框架控制，需要谨慎使用
      comObject.id = comObject.id || comObject.packageid + "_inner";
      //追加div
      var newComDiv = $("<div />");
      //追加设置属性
      newComDiv.attr("id", comObject.packageid);
      newComDiv.attr("idm-com-anchor-id", comObject.packageid);
      newComDiv.attr("idm-com-id", comObject.comId);
      newComDiv.attr("idm-com-classid", comObject.classId);
      newComDiv.attr("idm-com-classname", comObject.className);
      //追加组件标识
      newComDiv.addClass("idm_module");
      newComDiv.addClass(comObject.packageid);
      //追加内部div，提供给组件加载使用，只有ID，后续将不会再使用
      newComDiv.append($("<div />").attr("id", comObject.id));
      return newComDiv;
    },
    /**
     * 获取指定层级的组件树结构数据
     */
    getModuleTree(module, packageid) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      var _findModule;
      module.forEach((item) => {
        if (item.packageid == packageid) {
          _findModule = item;
        } else if (!_findModule && item.children) {
          _findModule = this.getModuleTree(item.children, packageid);
        }
      });
      return _findModule;
    },
    /**
     * 提供组件或组件内部容器的重新加载
     */
    moduleReload(packageid,innerContainerIndex){
      //先查找模块，然后根据innerContainerIndex来删除对应的加载状态标识
      const reloadModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      let reloadModuleArray = [reloadModule];
      let parentModule;
      if(innerContainerIndex||innerContainerIndex=="0"){
        //存在内部索引
        reloadModuleArray = [];
        reloadModule&&reloadModule.children&&reloadModule.children.forEach(item=>{
          if(item.containerIndex==innerContainerIndex){
            reloadModuleArray.push(item);
          }
        })
        parentModule = reloadModule;
      }else{
        //找出当前的父级
        let globalIdArray = reloadModule.globalId.split(".");
        const globalId = globalIdArray.length<=2?globalIdArray[0]:globalIdArray[globalIdArray.length-2];
        parentModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          globalId
        );
      }
      //删除全部加载状态
      this.resetLayoutInnerModuleLoadedStatus(reloadModuleArray);
      reloadModuleArray&&reloadModuleArray.forEach((item) => {
        $("#" + item.packageid).remove();
      });
      
      //调用重新加载
      this.initModuleTreeToPage(
        reloadModuleArray,
        true,
        parentModule,
        false
      );
    },
    /**
     * 组件挂载完成回调事件
     */
    moduleMountComplete(module) {
      let that = this;
      //把事件合并到map中
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if (item.packageid == module.packageid) {
          item.idmBroadcastMessage = module.idmBroadcastMessage;
          item.idmSetContextValue = module.idmSetContextValue;
          item.idmGetContextValue = module.idmGetContextValue;
        }
      });
      console.log("挂载完成，回调返回的数据", module);
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        module.packageid
      );
      if (opModule.children && opModule.children.length > 0) {
        //有子组件，代表是容器类组件，首先获取是否加载完成且有子元素inner
        const targetNode = $("#" + module.packageid);
        if (targetNode.find(":visible").length == 0) {
          //代表内容元素还是空的，没有加载完成
          var invcount = 0;
          var intval = setInterval(function () {
            invcount++;
            if (
              $("#" + module.packageid).find(":visible").length == 0 &&
              invcount <= 20
            ) {
              return;
            }
            clearInterval(intval);
            that.moduleMountCompleteCommon(module);
          }, 5);
        } else {
          //加载时序问题，延迟3毫秒
          setTimeout(() => {
            that.moduleMountCompleteCommon(module);
          }, 3);
        }
      } else {
          //加载时序问题，延迟3毫秒
          setTimeout(() => {
            that.moduleMountCompleteCommon(module);
          }, 3);
      }
      // this.initModuleInnerEventHandle(module);
      // class名称表示：
      // drag_container_empty：空容器类名
      // drag_container：容器，可拖拽过来
      // id作为class类名：样式设置
      // idm_module：所有组件最外层都有的类名
      // drag_container_inlineblock: 代表是行内容器

      // 属性 ：
      // idm-com-anchor-id：代表新生成的唯一组件ID
      // id:代表新生成的唯一组件ID
      // idm-com-id：组件的ID
      // idm-com-classid：组件的包名
      // idm-com-classname：组件的类名
      // idm-container-index：容器索引，在一个组件内有两个或以上的容器就会作用到
    },
    moduleMountCompleteCommon(module){
      var moduleDom = $("#" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(">*[idm-ctrl]");
      var idmCtrl = moduleInnerDom.attr("idm-ctrl"),
        idmCtrlId = moduleInnerDom.attr("idm-ctrl-id"),
        domId = moduleInnerDom.attr("id");
      if (!domId) {
        moduleInnerDom.attr("id", idmCtrlId);
      }
      switch (idmCtrl) {
        case "drag_container": //容器
          moduleDom.addClass("drag_container");
          break;
        case "drag_container_inlineblock": //行内容器
          moduleDom.addClass("drag_container drag_container_inlineblock");
          break;
        case "idm_module": //非容器组件
          break;
      }

      //如果没有加载完成则先判断下，没有完成则继续加载
      if (!this.pageModuleDataIsLoaded(null, true)) {
        this.$nextTick(function () {
          this.initModuleTreeToPage(
            null,
            true,
            this.PageSettingData.page.layout
          );
        });
      }else{
        this.requestPageCommonInterfaceData();
      }
    },
    /**
     * 加载组件内部容器组件事件
     * 参数：
     * module：{packageid:组件包id，组件内部使用的id}
     */
    initModuleInnerEventHandle(module) {
      let that = this;
      //获取是否存在内部容器组件
      var moduleDom = $("#" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(
        ".drag_container[idm-ctrl-id='" + module.id + "'][idm-ctrl-inner]"
      );
      if (moduleInnerDom.length > 0) {
        //内部组件重新追加，因为上面哪种加载方式会导致没加载完
        const opModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          module.packageid
        );
        this.resetLayoutInnerModuleLoadedStatus(opModule.children);
        if (opModule.children && opModule.children.length > 0) {
          opModule.children.forEach((item) => {
            $("#" + item.packageid).remove();
          });
          this.initModuleTreeToPage(opModule.children, true, opModule);
        }
      }
    },
    /**
     * 加载前重置所有加载状态
     */
    resetLayoutInnerModuleLoadedStatus(module) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      module.forEach((item) => {
        delete item.loaded;
        if (item.children && item.children.length > 0) {
          this.resetLayoutInnerModuleLoadedStatus(item.children);
        }
      });
    },
    /**
     * 通用自定义函数
     */
    commonCustomEventFunHandle(propData,name){
      let that = this;
      propData = propData||this.pagePropData;
      var customHandle = propData[name],resultData;
      customHandle &&
        customHandle.forEach((item) => {
          resultData = window[item.name] &&
            window[item.name].call(this, {
              ...that.commonParam(),
              customParam: item.param,
              _this: that
            });
        });
      return resultData
    },
    /**
     * 通用的url参数对象
     * 所有地址的url参数转换
     */
    commonParam(){
      let urlObject = IDM.url.queryObject();
      var params = {
        pageId:
          window.IDM.broadcast && window.IDM.broadcast.pageModule
            ? window.IDM.broadcast.pageModule.id
            : "",
        urlData: JSON.stringify(urlObject),
      };
      return params;
    },
  },
};
</script>
